<template>
    <div id="post-uploader">
      <!---->
        <v-row>
          <v-col cols="12" sm="6" class="upload-file-area">
            <v-card>
              <v-img v-show="uploadImage" :src="uploadImage" alt=""/>
            </v-card>
            <div>
              <label v-show="!uploadImage" class="file-upload-btn" >
                
                 <input class="input" type="file" @change="onFileChange"/>
              </label>
              <div>
                <div class="remove-area" depressed v-show="uploadImage">
                  <v-btn @click="remove" color="red">
                    <span class="remove-btn">
                      
                    </span>
                  </v-btn>
                </div>
              </div>
            </div>
          </v-col>
            <Loading v-show="loading"></Loading>
          <!---->
          <v-col cols="12" sm="6" class="post-form">
            <v-textarea
              v-model="postComment"
              label=""
              auto-grow
              outlined
              rows="1"
              row-height="15"
            ></v-textarea>
            <v-text-field v-model="title" label=""></v-text-field>
              <v-select class="select" v-model="category" :items="items" label="">
              </v-select>
                <br/>
                  <v-btn class="submit-btn" depressed color="#1E90FF" @click="addImgFirebase">
                    <span class="submit-btn-text"></span>
                  </v-btn>
          </v-col>
        </v-row>
      </div>
</template>
<script>
import Loading from '../Loading'
import {db,storage} from '../../firebase';

export default{
  name:'PostUpload',
    data(){
      return{
        //
        loading:false,

        fileName:'',
        imgDataUrl:'',
        uploadImage:'',
        //
        category:'null',
        items:[
          '',
          ''
        ],
        //
        title:'',
        //
        postComment:'',
      };
    },
    components:{
      Loading
    },
    methods:{
      //
      onFileChange(e){
        const files = e.target.files || e.dataTransfer.files;
        this.createImage(files[0]);
        this.fileName = files[0].name
      },
      createImage(file){
        const reader = new FileReader();
        reader.onload = e =>{
          this.uploadImage = e.target.result;
        };
        reader.readAsDataURL(file);
        reader.onloadend = (e) =>{
          this.imgDataUrl = e.target.result
          
        }
      },
      //
      remove(){
        this.uploadImage = false
      },
      
      //firebase
      addImgFirebase(){
        this.loading = true
        //uid
        const uid = this.$store.state.userUid.uid
        const name = this.fileName
        const imgDataUrl = this.imgDataUrl;
        //this.loading = true
        //posts
        const imgData={
          date:new Date(),
          title:this.title,
          comment:this.postComment,
          category:this.category,
          imgUrl:"",
          fileName:this.fileName,
          docId:"",
          likeCount:0,
          bookMark:0,
        }

       //
       const storageRef = storage.child(`${uid}/${name}`);
        //base64
        storageRef.putString(imgDataUrl,'data_url')
          //URL
          .then(()=>{
            storageRef.getDownloadURL().then((url)=>{
              //imgDataimgUrlURL
              imgData.imgUrl = url

            //store

           //postsimgData
            db.collection('posts').add(imgData)
            //postsIDdocRef
              .then((docRef)=>{
                db.collection('posts').doc(docRef.id).update({docId:docRef.id})
              //userspostsID
        const userCl = db.collection('users').doc(uid).collection('posts').doc(docRef.id);
              //posts
            userCl.set({
              date:new Date(),
                //imgDataimgUrl
                //imgDataimgUrl
                imgUrl:imgData.imgUrl,
                title:imgData.title,
                docId:docRef.id
              })
              .then(()=>{
                this.loading = false
                alert('')
                location.reload();
              })
            })
            .catch(function(error){
              console.log(error)
            })

          })
       })
      }
    }
}
</script>
